@font-face {
  font-family: "PT Mono";
  font-display: monospace;
  src: local("PT Mono"), local("PTMono-Regular"), url("../../fonts/ptmono.woff2") format("woff2"), url("../../fonts/ptmono.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "PT Mono";
  font-display: monospace;
  src: local("PT Mono Bold"), local("PTMono-Bold"), url("../../fonts/ptmonobold.woff2") format("woff2"), url("../../fonts/ptmonobold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Roboto Condensed";
  font-display: "Arial";
  src: local("Roboto Condensed"), local("RobotoCondensed-Regular"), url("../../fonts/Robotocondensed.woff2") format("woff2"), url("../../fonts/Robotocondensed.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Roboto Condensed";
  font-display: "Arial";
  src: local("Roboto Condensed Bold"), local("RobotoCondensed-Bold"), url("../../fonts/Robotocondensedbold.woff2") format("woff2"), url("../../fonts/Robotocondensedbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Courier New", monospace;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  padding: 10px 20px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-style: normal;
  line-height: normal;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  border: 1px solid rgba(255, 255, 255, 0);
  background-color: #00b2ff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn--fz-14 {
  font-size: 14px;
  letter-spacing: 0.7px;
}
.btn--fz-16 {
  font-size: 16px;
  letter-spacing: 0.8px;
}
.btn--fz-18 {
  font-size: 18px;
  letter-spacing: 0.9px;
}
.btn--dark {
  background-color: #041427;
  color: #ffffff;
}
.btn--blue {
  background-color: #00b2ff;
  color: #f2f5f6;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    background-color: #ffffff;
    color: #00b2ff;
  }
}
.btn.focus-visible:focus {
  background-color: #ffffff;
  color: #00b2ff;
}
.btn--transparent {
  background-color: rgba(255, 255, 255, 0);
}
@media (hover: hover) {
  .btn--transparent:hover:not(.focus-visible) {
    background-color: #00b2ff;
    color: #f2f5f6;
    border: 1px solid #00b2ff;
  }
}
.btn--transparent.focus-visible:focus {
  background-color: #00b2ff;
  color: #f2f5f6;
  border: 1px solid #00b2ff;
}
.btn--transparent-white {
  border: 1px solid #ffffff;
}
.btn--transparent-blue {
  color: #00b2ff;
  border: 1px solid #00b2ff;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  color: #ffffff;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 38px 40px 40px 40px;
  background-color: #00b2ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 17px;
  right: 16px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  line-height: 140.625%;
  margin-top: 0;
  margin-bottom: 3px;
}

.modal__desc {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 134.4%;
  margin-bottom: 40px;
}

.modal__desc p {
  margin-top: 0;
}
.modal__desc p:last-child {
  margin-bottom: 0;
}

.modal .custom-input {
  margin-bottom: 20px;
}

.modal .custom-textarea {
  margin-bottom: 20px;
}

.modal__btn {
  width: 100%;
  margin-bottom: 20px;
  padding: 13px 20px;
}

.modal .custom-toggle {
  margin-left: 8px;
}

.custom-input,
.custom-textarea {
  width: 100%;
  position: relative;
  font-size: 16px;
  border-radius: 3px;
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  color: #f2f5f6;
}
.custom-input::placeholder,
.custom-textarea::placeholder {
  color: #ffffff;
}

.custom-input {
  padding: 15px;
}

.custom-textarea {
  padding: 25px 15px;
  resize: none;
  min-height: 155px;
}
@media (max-width: 1023px) {
  .custom-textarea {
    padding-top: 33px;
  }
}
@media (max-width: 767px) {
  .custom-textarea {
    padding-top: 25px;
    min-height: 180px;
  }
}

.custom-toggle {
  position: relative;
  font-size: 14px;
  line-height: normal;
  font-weight: 400;
  color: #ffffff;
  display: flex;
  align-items: center;
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  padding-left: 25px;
  cursor: pointer;
}
.custom-toggle--checkbox .custom-toggle__icon {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 1.5px;
  transition: border 0.3s ease;
}
.custom-toggle input:checked + .custom-toggle__icon {
  background-image: url("../img/sprite/icon-check.svg");
  background-size: 15px;
  background-position: center;
}

.header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  width: 100%;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  padding-top: 30px;
  padding-bottom: 35px;
}
@media (max-width: 1023px) {
  .header {
    padding-top: 20px;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 12px;
  }
}

.header__inner {
  display: flex;
  align-items: center;
}

.header__link {
  display: flex;
  align-items: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  color: #f2f5f6;
  transition: color 0.3s ease;
  margin-bottom: 7px;
}
@media (hover: hover) {
  .header__link:hover:not(.focus-visible) {
    color: #00b2ff;
  }
}
.header__link.focus-visible:focus {
  color: #00b2ff;
}
.header__link--email {
  min-width: 225px;
  margin-right: 27px;
}
@media (max-width: 1439px) {
  .header__link--email {
    margin-left: auto;
  }
}
@media (max-width: 1023px) {
  .header__link--email {
    margin-left: 0;
    min-width: auto;
  }
}
.header__link--phone {
  margin-right: 166px;
}
@media (max-width: 1439px) {
  .header__link--phone {
    margin-right: auto;
  }
}
@media (max-width: 1023px) {
  .header__link {
    justify-content: center;
    width: 40px;
    height: 40px;
    font-size: 0;
    margin-bottom: 17px;
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .header__link {
    margin-bottom: 0;
    margin-right: 15px;
  }
}

.header__link svg {
  flex-shrink: 0;
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .header__link svg {
    margin-right: 0;
  }
}

.header__btn {
  margin-bottom: 7px;
  padding: 11px 16px;
}
@media (max-width: 1023px) {
  .header__btn {
    display: none;
  }
}

.footer {
  background-color: #041427;
  padding-top: 50px;
  padding-bottom: 55px;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 1023px) {
  .footer {
    padding-bottom: 64px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding-top: 35px;
    padding-bottom: 35px;
  }
}

@media (max-width: 767px) {
  .footer .container {
    padding-left: 0;
    padding-right: 0;
  }
}

.footer__inner {
  display: flex;
}
@media (max-width: 1023px) {
  .footer__inner {
    flex-wrap: wrap;
  }
}

.footer__block--logo {
  margin-right: 160px;
  flex-shrink: 0;
}
@media (max-width: 1439px) {
  .footer__block--logo {
    margin-right: 30px;
  }
}
@media (max-width: 1023px) {
  .footer__block--logo {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-right: 0;
    margin-bottom: 38px;
  }
}
@media (max-width: 767px) {
  .footer__block--logo {
    flex-direction: column;
    justify-content: flex-start;
    margin-bottom: 24px;
  }
}

.footer__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0.75px;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0;
}

.footer__block--menu {
  padding-top: 7px;
  margin-right: 30px;
}
@media (max-width: 1439px) {
  .footer__block--menu {
    margin-left: auto;
    margin-right: auto;
  }
}
@media (max-width: 1023px) {
  .footer__block--menu {
    width: 50%;
    padding-top: 0;
    margin-top: 0;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .footer__block--menu {
    width: 100%;
    padding-top: 22px;
    padding-bottom: 21px;
    margin-right: 0;
    border-top: 1px solid rgba(255, 255, 255, 0.7);
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
  }
}

.main-menu {
  display: flex;
}
@media (max-width: 1023px) {
  .main-menu {
    display: block;
  }
}

.main-menu__col {
  list-style-type: none;
  width: 220px;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 30px;
}
@media (max-width: 767px) {
  .main-menu__col {
    width: auto;
  }
}
.main-menu__col:last-child {
  margin-right: 0;
}

.main-menu__item {
  margin-bottom: 6px;
}
.main-menu__item:last-child {
  margin-bottom: 0;
}

.footer__link {
  color: rgba(255, 255, 255, 0.7);
  font-family: PT Mono;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  transition: color 0.3s ease;
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible) {
    color: #00b2ff;
  }
}
.footer__link.focus-visible:focus {
  color: #00b2ff;
}
.footer__link--menu {
  line-height: 22px;
}
.footer__link--phone {
  display: block;
  line-height: 24px;
  margin-bottom: 6px;
}

.footer__block--contact {
  padding-top: 7px;
}
@media (max-width: 1023px) {
  .footer__block--contact {
    width: 48%;
    margin-left: 0;
    padding-top: 0;
  }
}
@media (max-width: 767px) {
  .footer__block--contact {
    width: 100%;
    padding-top: 22px;
    padding-bottom: 9px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
